const swiperHero = new Swiper('.hero-container', {
  loop: true,
  slidesPerView: 1,
  slidesPerGroup: 1,
  noSwipingClass: 'hero-container',
  effect: 'cube',
  cubeEffect: {
    slideShadows: false,
  },
  navigation: {
    nextEl: '.swiper-button-next',
    prevEl: '.swiper-button-prev',
  },
  autoplay: {
    delay: 5000,
  },
  breakpoints: {
    320: {
      enabled: false
    },
    993: {
      enabled: true
    }
  }
});
